.clsLoginForms {
    background-image: url('https://cdn.pixabay.com/photo/2016/06/02/02/33/triangles-1430105_640.png');
    height: 100vh;
    width: 100vw;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

.clsLoginFormCnt {
    background-color: transparent;
    border: 0.1em solid black;
    backdrop-filter: blur(5px);
    position: relative;
    width: 40%;
    height: 40%;
}

.clsEmailTextBox,
.clsPasswordTextBox {
    margin-bottom: 3em;
    color: black;
    width: 50%;
}

.clsLoginFormHeaderTitle {
    margin-bottom: 15%;
    color: white;
    font-weight: 500;
    font-size: large;
}

.clsFormHeaderTitle {
    color: white;
    margin-bottom: 3em;
    margin-top: 1em;
    font-weight: 700;
}